import classNames from 'classnames/bind'

import Form from "../../components/Form";
import setTitle from "../../utils/setTitle";
import styles from './index.module.scss'

const cx = classNames.bind(styles)

function SignUp({ title }) {
    setTitle(title)

    const data = {
        title: 'ng k',
        list: [
            {
                label: 'S in thoi',
                type: 'text',
                name: 'user-name',
                placeholder: 'Nhp s in thoi',
            },
            {
                label: 'Mt khu',
                type: 'password',
                name: 'password',
                placeholder: 'Nhp mt khu',
            },
            {
                label: 'Nhp li mt khu',
                type: 'password',
                name: 'repassword',
                placeholder: 'Nhp li mt khu',
            },
        ],
        actions: [
            {
                to: '/dang-nhap',
                size: 'md',
                children: 'ng k',
            },
        ],
    }

    return (
        <div className={cx('wrapper')}>
            <Form data={data} />
        </div>
    );
}

export default SignUp;