import { useEffect, useState } from 'react';
import classNames from 'classnames/bind'

import * as productService from '../../services/productService'
import * as categoryService from '../../services/categoryService'

import setTitle from '../../utils/setTitle';
import CategoryItem from '../../components/Category';
import ProductItem from '../../components/Product';
import Container from '../../components/Container';
import Group from '../../components/Group';
import Page from '../../components/Page';
// import Carousel from '../../components/Carousel';
import styles from './index.module.scss'

const cx = classNames.bind(styles)

function Home({ title }) {
    setTitle(title)

    const [categories, setCategories] = useState([])
    const [products, setProducts] = useState([])

    useEffect(() => {
        const fetchApi = async () => {
            try {
                const arrayCategories = await categoryService.getCategories()
                setCategories(arrayCategories)
            } catch (e) {

            }
        }
        fetchApi()
    }, [])

    useEffect(() => {
        const fetchApi = async () => {
            try {
                const arrayProducts = await productService.getProducts()
                setProducts(arrayProducts)
            } catch (e) {

            }
        }
        fetchApi()
    }, [])

    return (
        <Page>
            {/* <Carousel /> */}
            <div className={cx('carousel')}>
                {/* <ul>
                    <li>S nghin cu li ci slide, v dng th vin slick react nn slide hi c chui, t build bng reactjs s tt hn</li>
                    <li> dng API t <a href="https://api-nghiquan.herokuapp.com/">https://api-nghiquan.herokuapp.com/</a> (mnh t build bng nodejs nh)</li>
                </ul> */}
            </div>
            <Container>
                <Group classNameTitle={cx('title')} title='top Sales off' column="5">
                    {
                        products.map((item, index) => (
                            <ProductItem key={index} data={item} />
                        ))
                    }
                </Group>
            </Container>
            <Container>
                <Group classNameTitle={cx('title')} title='top sn phm mi nht' column="6">
                    {
                        products.map((item, index) => (
                            <ProductItem key={index} data={item} />
                        ))
                    }
                </Group>
            </Container>
            <Container className={cx('container')}>
                <Group className={cx('category-sidebar')} title='Danh mc' column="1" space="4">
                    {
                        categories.map((item, index) => (
                            <CategoryItem key={index} data={item} />
                        ))
                    }
                </Group>
                <Group className={cx('product-list')} classNameTitle={cx('title')} title="Gi  cho bn" column="4">
                    {
                        products.map((item, index) => (
                            <ProductItem key={index} data={item} />
                        ))
                    }
                </Group>
            </Container>
        </Page>
    );
}

export default Home;